import React from 'react'
import {Layout} from 'antd'
import NavBar from '../../components/NavBar'

import {Switch, Route, Redirect} from 'react-router-dom'
import ExerciseSetsDashboard from "./ExerciseSets";
import ExerciseSetManage from "./ExerciseSetManage";

const {Content} = Layout

function Dashboard() {

    return <Layout className={'page'}>
        <NavBar/>
        <Content>
            <Switch>
                <Route path={'/dashboard/exerciseSets/:id'} component={ExerciseSetManage}/>
                <Route path={'/dashboard/exerciseSets'} component={ExerciseSetsDashboard}/>
                <Redirect to={'/dashboard/exerciseSets'}/>
            </Switch>
        </Content>
    </Layout>
}

export default Dashboard