import React, {useEffect, useState} from 'react'
import {PageHeader, Row, Col, Button, Layout, Menu} from 'antd'
import {AppstoreOutlined} from '@ant-design/icons'
import {Link, Switch, Route, Redirect} from 'react-router-dom'

import ExerciseSets from './Exercise/Sets'
import styles from './index.module.less'
import ExerciseSetDetail from "./Exercise/SetDetail";
import NavBar from '../components/NavBar'

const {Sider, Content, Header} = Layout


function Home() {

    return <Layout className={'page'}>
         <NavBar />
        <Layout>
            <Sider width={230}>
                <Menu theme={'dark'}>
                    <Menu.Item>
                        <AppstoreOutlined/>
                        <span>题库</span>
                    </Menu.Item>
                    <Menu.Item>
                        <AppstoreOutlined/>
                        <span>支持作者</span>
                    </Menu.Item>
                </Menu>
            </Sider>
            <Content className={styles.content}>
                <Switch>
                    <Route path={'/exerciseSets/:id'} component={ExerciseSetDetail}/>
                    <Route path={'/exerciseSets'} component={ExerciseSets}/>
                    <Redirect to={'/exerciseSets'}/>
                </Switch>
            </Content>
        </Layout>
    </Layout>
}


export default Home