import React, {useState, useEffect} from 'react'
import {Table, Button, Modal, Form, Input, Card, message} from 'antd'
import {Link} from 'react-router-dom'

function ExerciseSetsDashboard() {


    const [createVisible, setCreateVisible] = useState(false)
    const [exerciseSets, setExerciseSets] = useState([])
    const [createForm] = Form.useForm()



    const showCreateModal = () => {
        setCreateVisible(true)
        createForm.resetFields()
    }


    const fetchData = () => {
        window.http.get('/exerciseSets/').then(response => {
            setExerciseSets(response.data.results)
        })
    }

    const onCreate = (values) => {
        window.http.post('/exerciseSets/', values).then(response => {
            setCreateVisible(false)
            message.success("创建成功")
            fetchData()
        })
    }

    useEffect(() => {
        fetchData()
    }, [])

    return <div>

        <Card>
            <div className={"toolbar mb20"}>
                <h3>我创建的题集</h3>

                <Button
                    type={'primary'}
                    onClick={showCreateModal}>新建题集</Button>
            </div>
            <Table
                dataSource={exerciseSets}
                columns={[
                    {
                        title: "题目名称",
                        key: "name",
                        dataIndex:"name"
                    },
                    {
                        title:"操作",
                        render:(text, record)=>{
                            return <div>
                                <Link to={'/dashboard/exerciseSets/' + record.id}>编辑</Link>
                            </div>
                        }
                    }

                ]}/>
        </Card>
        <Modal
            onOk={onCreate}
            footer={null}
            title={'创建题集'}
            onCancel={() => setCreateVisible(false)}
            visible={createVisible}>

            <Form
                onFinish={onCreate}
                form={createForm}>
                <Form.Item
                    name={'name'}
                    rules={[{
                        required: true, message: "请输入名称"
                    }]}>
                    <Input
                        placeholder={'题集名称'}/>
                </Form.Item>
                <Form.Item>
                    <Button
                        htmlType={'submit'}
                        type={'primary'}>创建</Button>
                </Form.Item>
            </Form>
        </Modal>
    </div>

}

export default ExerciseSetsDashboard