
import http from './http'

export function signupApi(data) {
   return http.post('/signUpEmail/', data)
}

export function loginApi(data) {
    return http.post('/token', data)
}

export function forgetEmailApi(data) {
    return http.post('/api/forget/email', data)
}

export function forgetSubmitApi(data) {
    return http.post('/api/forget/submit', data)
}