import React, {useState, useEffect} from 'react'
import {PageHeader, Drawer, message, Card, Select, Table, Modal, Button, Form, Input} from 'antd'
import {withRouter} from 'react-router-dom'


function EditChoiceExerciseDrawer({visible, onClose, onEdit, exercise}) {

    let [addOptionVisible, setAddOptionVisible] = useState(false)
    let [options, setOptions] = useState([])

    let [form] = Form.useForm()

    let fetchOptions = () => {
        window.http.get('/choiceExerciseOptions/', {exercise: exercise.id}).then(response => {
            setOptions(response.data.results)
        })
    }

    let onCreateOption = (values) => {
        window.http.post('/choiceExerciseOptions/', {
            ...values,
            exercise: exercise.id,
            no: options.length + 1
        }).then(response => {
            message.success('创建选项成功')
            setAddOptionVisible(false)
            fetchOptions()
        })
    }

    let onSaveQuestion = () => {
        window.http.put(`/exercises/${exercise.id}/`, {
            ...exercise,
            question: form.getFieldValue("question")
        }).then(response => {
            message.success("保存成功")
            onEdit()
        })
    }

    let onSaveAnswer = () => {

        let answer = form.getFieldValue("answer")
        let explain = form.getFieldValue("explain")

        window.http.put(`/choiceExerciseAnswers/${exercise.id}/`, {
            answer,
            explain
        }).then(response => {
            message.success("保存成功")
        })
    }

    useEffect(() => {
        fetchOptions()

        form.setFieldsValue({
            question: exercise.question
        })
    }, [visible])


    return <Drawer
        width={500}
        destroyOnClose={true}
        title={'编辑题目'}
        onClose={onClose}
        visible={visible}>

        <Form form={form}>
            <Form.Item name={'question'}>
                <Input.TextArea
                    rows={5}/>
            </Form.Item>
            <Form.Item className={'alignRight'}>
                <Button onClick={onSaveQuestion}>保存</Button>
            </Form.Item>
            {options.map(option => {
                return <div>{option.content}</div>
            })}
            <Form.Item>
                <Button
                    onClick={() => {
                        setAddOptionVisible(true)
                    }}
                    block={true}>新建题目</Button>
            </Form.Item>


            <Form.Item name={"answer"}>
                <Input placeholder={'答案'}/>
            </Form.Item>
            <Form.Item name={'explain'}>
                <Input.TextArea
                    placeholder={"解释"}
                    rows={3}/>
            </Form.Item>
            <Form.Item className={"alignRight"}>
                <Button
                    onClick={onSaveAnswer}
                    type={'primary'}>保存答案</Button>
            </Form.Item>
        </Form>

        <Drawer
            title={'新建选项'}
            onClose={() => {
                setAddOptionVisible(false)
            }}
            visible={addOptionVisible}>
            <Form onFinish={onCreateOption}>
                <Form.Item
                    rules={[
                        {required: true, message: "请输入内容"}
                    ]}
                    name={'content'}>
                    <Input/>
                </Form.Item>
                <Form.Item>
                    <Button
                        type={'primary'}
                        htmlType={'submit'}>创建</Button>
                </Form.Item>
            </Form>
        </Drawer>

    </Drawer>
}

function AddExerciseModal({visible, onCancel, sid, onSuccess}) {

    const [addExerciseType, setAddExerciseType] = useState('choice')
    const onCreateExercise = (values) => {
        window.http.post('/exercises/', {
            ...values,
            set: sid,
            no: 1,
            type: addExerciseType
        }).then(response => {
            message.success("创建成功")
            onCancel()
            onSuccess()
        })
    }
    const [addExerciseForm] = Form.useForm()

    return <Modal
        footer={null}
        onCancel={onCancel}
        title={'添加题目'}
        visible={visible}>
        <Form onFinish={onCreateExercise}>
            <Form.Item name={'name'}
                       rules={[
                           {
                               required: true, message: "请输入题目名称"
                           }
                       ]}>
                <Input placeholder={'题目名称'}/>
            </Form.Item>
            <Form.Item name={'type'}>
                <Select
                    defaultValue={addExerciseType}
                    onChange={e => setAddExerciseType(e)}
                    value={addExerciseType}>
                    <Select.Option value={'choice'}>选择题</Select.Option>
                    <Select.Option value={'input'}>填空题</Select.Option>
                    <Select.Option value={'code'}>代码题</Select.Option>
                </Select>
            </Form.Item>
            <Form.Item>
                <Button
                    htmlType={'submit'}
                    type={'primary'}>
                    提交
                </Button>
            </Form.Item>
        </Form>
    </Modal>
}


function ExerciseSetManage(props) {

    const [createVisible, setCreateVisible] = useState(false)
    const [exercises, setExercises] = useState([])
    const [editingExercise, setEditingExercise] = useState({})
    const [choiceExerciseVisible, setChoiceExerciseVisible] = useState(false)
    const [exerciseSetInfo, setExerciseSetInfo] = useState({})


    const fetchExercise = () => {
        window.http.get('/exercises', {
            set: props.match.params.id
        }).then(response => {
            setExercises(response.data.results)
        })
    }

    const onShowEdit = (exercise) => {
        switch (exercise.type) {
            case 'choice':
                setChoiceExerciseVisible(true)
                setEditingExercise(exercise)
        }
    }

    const onDelete = (exercise) => {
        Modal.confirm({
            title: "是否删除题目",
            onOk: () => {
                window.http.delete('/exercises/' + exercise.id).then(response => {
                    message.success('删除成功')
                    fetchExercise()
                })

            }
        })
    }

    useEffect(() => {
        fetchExercise()

        window.http.get('/exerciseSets/' + props.match.params.id).then(response => {
            setExerciseSetInfo(response.data)
        })

    }, [])


    return <Card>
        <PageHeader
            extra={[
                <Button onClick={() => setCreateVisible(true)}>
                    新建题目
                </Button>
            ]}
            onBack={() => {
                props.history.push("/dashboard/exerciseSets")
            }}
            title={exerciseSetInfo.name}/>

        <Table
            dataSource={exercises}
            columns={[
                {
                    title: "题目名称",
                    key: "name",
                    dataIndex: "name"
                },
                {
                    title: "题目类型",
                    key: "type",
                    dataIndex: "type",
                    render(text) {
                        switch (text) {
                            case  "choice":
                                return "选择题"
                        }
                    }
                },
                {
                    title: "操作",
                    render: (text, record) => {
                        return <div>
                            <a
                                className={"mr20"}
                                onClick={() => onShowEdit(record)}>编辑</a>
                            <a onClick={() => onDelete(record)}>删除 </a>
                        </div>
                    }
                }
            ]}/>

        {/* 添加题目 */}
        <AddExerciseModal
            sid={props.match.params.id}
            onCancel={() => setCreateVisible(false)}
            onSuccess={() => fetchExercise()}
            visible={createVisible}/>

        <EditChoiceExerciseDrawer
            onEdit={() => fetchExercise()}
            exercise={editingExercise}
            onClose={() => {
                setEditingExercise({})
                setChoiceExerciseVisible(false)
            }}
            visible={choiceExerciseVisible}/>
    </Card>
}

export default withRouter(ExerciseSetManage)