import React, {useContext} from 'react'
import {Button, Avatar, Dropdown, Menu, Layout} from 'antd'
import {withRouter, Link} from 'react-router-dom'


import {UserContext} from '../../contexts'
import styles from './index.module.less'

const {Header} = Layout

function NavBar() {

    const user = useContext(UserContext)


    const onLogout = () => {
        user.setToken("")
        localStorage.removeItem("token")
    }

    const signLinks = () => {
        return <div>
            <Link to={'/signin'}>
                <Button className={'mr20'}>登录</Button>
            </Link>
            <Link to={'/signup'}>
                <Button>注册</Button>
            </Link>
        </div>
    }

    const avatarBox = () => {

        const menu = (
            <Menu>
                <Menu.Item className={styles.avatarMenuItem}>
                    意见反馈
                </Menu.Item>
                <Menu.Item
                    className={styles.avatarMenuItem}
                    onClick={onLogout}>
                    退出
                </Menu.Item>
            </Menu>
        )
        return <Dropdown
            placement={'bottomRight'}
            overlay={menu}>
            <Avatar
                src={require('./avatar.jpeg')}
                className={styles.avatar}
                size={40}/>
        </Dropdown>
    }

    return <Header className={styles.header} theme={'light'}>
        <Link to={'/'}>
            <img src={require('../../assets/logo.png')}/>
        </Link>

        {user.token ? avatarBox() : signLinks()}
    </Header>
}


export default NavBar