import React, {useState} from 'react'
import {Link} from 'react-router-dom'
import {Form, Steps, Input, Button, Row, Col, Alert, notification, Result} from 'antd'
import DefaultLayout from "../../layouts/DefaultLayout";
import {forgetEmailApi, forgetSubmitApi} from "../../requests/user";

const {Step} = Steps

export default function () {

    let [current] = useState(0)
    let [emailLoading, setEmailLoading] = useState(false)
    let [submitLoading, setSubmitLoading] = useState(false)
    let [success, setSuccess] = useState(false)
    let [error, setError] = useState('')
    let [form] = Form.useForm()

    let onGetForgetEmail = () => {
        setError("")
        form.validateFields(['email']).then(values => {
            setEmailLoading(true)
            forgetEmailApi(values).then(response => {
                setEmailLoading(false)
                notification.success({
                    message: "验证码已发送至邮箱，请前往邮箱查看"
                })
            }).catch(err => {
                setEmailLoading(false)
                setError(err.response.data.msg)
            })
        }).catch(err => {

        })
    }

    let onSubmit = (values) => {
        setSubmitLoading(true)
        setError("")
        forgetSubmitApi(values).then(response => {
            setSubmitLoading(false)
            setSuccess(true)
        }).catch(err => {
            setSubmitLoading(false)
            setError(err.response.data.msg)
        })
    }

    const ForgetForm = () => {
        return <Form form={form} onFinish={onSubmit}>
            <Form.Item
                rules={[
                    {required: true, message: "请输入邮箱"}
                ]}
                name={'email'}>
                <Input
                    placeholder={'请输入邮箱'}
                    size={'large'}/>
            </Form.Item>
            <Form.Item>
                <Row gutter={8}>
                    <Col span={14}>
                        <Form.Item name={'code'} rules={[
                            {required: true, message: "请输入验证码"}
                        ]}>
                            <Input placeholder={'请输入验证码'}
                                   size={'large'}/>
                        </Form.Item>
                    </Col>
                    <Col span={10}>
                        <Button block={true}
                                loading={emailLoading}
                                onClick={onGetForgetEmail}
                                size={'large'}>获得验证码邮箱</Button>
                    </Col>
                </Row>
            </Form.Item>
            <Form.Item name={'password'} rules={[
                {required: true, message: "请输入密码"}
            ]}>
                <Input placeholder={'请输入新密码'}
                       type={'password'}
                       size={'large'}/>
            </Form.Item>
            <Form.Item>
                <Button type={'primary'}
                        size={'large'}
                        htmlType={"submit"}
                        block={true}>完成修改</Button>
            </Form.Item>
            {error ? <Alert type={'error'} showIcon message={error}/> : null}
        </Form>
    }


    return <DefaultLayout title={"忘记密码"}>
        <div className={'main-form'}>
            {success ? <Result
               status={"success"}
               extra={[<Link to={'/signin'}>
                   <Button type={'primary'}>前往登录</Button>
               </Link>]}
               title={"重置密码成功"}
            /> : <ForgetForm/>}
        </div>
    </DefaultLayout>
}