import React, {useEffect, useState} from 'react'
import {Layout, Button, Radio} from 'antd'


import Navbar from '../../components/NavBar'
import styles from './Main/index.module.less'

const {Content, Footer} = Layout

function ExerciseMain(props) {


    let [options, setOptions] = useState([])
    let [exercise, setExercise] = useState({})
    let [setInfo, setSetInfo] = useState({})

    let abcdef = ['A', 'B', 'C', 'D']

    useEffect(() => {
        window.http.get('/choiceExerciseOptions', {
            exercise: props.match.params.eid
        }).then(response => {
            setOptions(response.data.results)
        })

        window.http.get('/exercises/' + props.match.params.eid).then(response => {
            setExercise(response.data)
        })

        window.http.get('/exerciseSets/' + props.match.params.sid).then(response=>{
            setSetInfo(response.data)
        })
    }, [])

    return <Layout className={'page'}>
        <Navbar/>

        <Content className={styles.content}>
            <div className={styles.inner}>
                <div>
                    {exercise.question}
                </div>
                <div className={styles.options}>
                    <Radio.Group name="radiogroup" defaultValue={1}>
                        {options.map((option, index) => {
                            return <Radio className={styles.option} value={1}>{option.content}</Radio>
                        })}
                    </Radio.Group>
                </div>

                <div className={'alignRight'}>
                    <Button type={'primary'}>提交</Button>
                </div>
            </div>
        </Content>
        <Footer className={styles.footer}>
            <Button>上一题</Button>
            <div>
                /{setInfo.num}
            </div>
            <Button>下一题</Button>
        </Footer>
    </Layout>
}

export default ExerciseMain