import React, {useState, useEffect} from 'react'
import {Table, Card, Row, Col, PageHeader} from 'antd'
import {withRouter, Link} from 'react-router-dom'
import styles from './SetDetail/index.module.less'

function ExerciseSetDetail(props) {

    let [exercises, setExercises] = useState([])
    let [exerciseSetInfo, setExerciseSetInfo] = useState({})
    useEffect(() => {
        window.http.get('/exercises').then(response => {
            setExercises(response.data.results)
        })

        window.http.get(`/exerciseSets/${props.match.params.id}/`).then(response => {
            setExerciseSetInfo(response.data)
        })
    }, [])


    return <div>
        <PageHeader
            title={exerciseSetInfo.name}
            onBack={() => props.history.push('/')}/>
        <div className={styles.container}>
            <Table
                columns={[
                    {
                        title: "题目名称",
                        dataIndex: "name"
                    },
                    {
                        title: "题目操作",
                        render: (t, exercise)=> {
                            return <div>
                                <Link to={`/exerciseSets/${props.match.params.id}/${exercise.id}`}>前往练习</Link>
                            </div>
                        }
                    }
                ]}
                dataSource={exercises}/>
        </div>
    </div>
}

export default withRouter(ExerciseSetDetail)