import http from './http'



export function fetchExerciseSets() {
    return http.get('/exerciseSets')
}


export function fetchExerciseSetDetail(setId) {
    return http.get('/exerciseSets/' + setId)
}

export function startExerciseApi(id) {
    return http.get('/api/exercises/' + id + "/start")
}

export function submitCodeApi(id, data) {
    return http.post('/api/exercises/' + id + "/submit", data)
}

export function restartExerciseApi(id) {
    return http.post('/api/exercises/' + id + "/restart")
}