import React, {useEffect, useState} from 'react'
import {Link} from 'react-router-dom'

import {
    Result, Button
} from 'antd'


function UserActive(props) {

    let [status, setStatus] = useState(0)
    let [error, setError] = useState(false)
    useEffect(() => {
        window.http.post('/signUpActive/', {
            token: props.match.params.token
        }).then(response => {
            setStatus(1)
        }).catch(err => {
            setStatus(-1)
            setError(err.response.data.detail)
        })
    }, [])


    const gotoSignIn = () => {

    }

    switch (status) {

        case 0:
            return <Result
                title={"激活中...."}
            />

        case -1:
            return <Result
                status={"error"}
                title={error}
            />
        case 1:
            return <Result
                status={"success"}
                title={'激活成功'}
                extra={[
                    <Link to={'/signin'}>
                        <Button>前往登录</Button>
                    </Link>
                ]}
            />
    }
}


export default UserActive