import React, {useState, useEffect} from 'react'
import {Row, Col} from 'antd'
import {Link} from 'react-router-dom'

import styles from './Sets/index.module.less'
import {fetchExerciseSets} from "../../requests/exercise";


function ExerciseSets() {

    let [exerciseSets, setExerciseSets] = useState([])

    useEffect(() => {
        fetchExerciseSets().then(response => {
            setExerciseSets(response.data.results)
        })
    }, [])
    return <div className={styles.container}>

        <Row gutter={32}>
            {exerciseSets.map(set => {
                return <Col xs={24} sm={24} md={8}>
                    <Link to={'/exerciseSets/' + set.id}>
                        <div className={styles.card}>
                            <div className={styles.cardTop}>
                                <img src={require('../../assets/images/js.png')}/>
                                <h2>
                                    {set.name}
                                </h2>
                            </div>
                        </div>
                    </Link>
                </Col>
            })}
        </Row>
    </div>
}

export default ExerciseSets